<template>
    <div id="app">
        <router-view class="main" :class="{'hide': hide}"></router-view>
        <MainToolbar class="toolbar" :class="{'hide': hide}"></MainToolbar>
        <AddQian :hide="hide" :position="{x: 16, y: -16 * 4}"></AddQian>
    </div>
</template>

<script>
import AddQian from 'cpn/button/AddQian';
import MainToolbar from 'cpn/toolbar/MainToolbar.vue';

export default {
    name: 'app',
    components: {
        AddQian,
        MainToolbar
    },
    data() {
        return {
            hide: false
        };
    },
    watch: {
        '$route'(to) {
            if (to.name === 'newQian') {
                this.hide = true;
            }
            else {
                this.hide = false;
            }
        }
    },
    methods: {

    }
};
</script>

<style lang="less">
@import "./assets/less/global.less";

#app {
    width: 100%;
    height: 100%;

    .main {
        width: 100%;
        height: calc( 100% - 3.5em );
        &.hide {
            height: 100%;
        }
    }
    .toolbar {
        width: 100%;
        height: 3.5em;
        &.hide {
            height: 0;
            display: none;
        }
    }
}
</style>
